
export const TextInput = props => {
  return (
    <>
      {props.label && <div className="font-bold pb-5">{props.label}</div>}
      <input type="text" className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" {...props} />
    </>
  )
}