import { useState } from "react"
import { replaceBBCode } from "../../../../helpers/bbcode"

export const SelectFromRange = (data) => {
  const [currentValue, setCurrentValue] = useState(0)
  const { name, label, options, onValueSelect, ...otherAttrs } = data

  const handleChange = (e) => {
    setCurrentValue(e.target.value)
    return onValueSelect(e)
  }

  return (<div key={name} className="p-0 w-full">
    <div className="max-w-sm pb-5 font-bold" dangerouslySetInnerHTML={{
      __html: replaceBBCode(label) || ' '
    }}></div>

    <div className="w-full flex">
      {options.map((opt, idx) => (
        <div className="w-full border border-solid" key={idx}>
          <div className="h-16 font-bold text-center whitespace-pre" dangerouslySetInnerHTML={{
            __html: replaceBBCode(opt.label) || ' '
          }}>
          </div>
          <div className="text-center"><input type="radio" name={name} value={opt.value} onChange={handleChange} /></div>
          <div className="text-center">{opt.value}</div>
        </div>
      ))}
    </div>
  </div>)
}