import { useState } from "react"

export const Slider = ({ name, min, max, label, unit, onSlideChange, ...otherAttrs }) => {
  const [currentSliderValue, setCurrentSliderValue] = useState(Math.ceil(max / 2))

  const handleSlideChange = (e) => {
    setCurrentSliderValue(e.target.value)
    return onSlideChange(e)
  }

  return (<div key={name} className="p-0">
    <div className="max-w-sm font-bold pb-5">{label}</div>
    <input
      type="range"
      name={name}
      min={min}
      max={max}
      onChange={handleSlideChange}
      {...otherAttrs}
    />&nbsp;
    <span>{currentSliderValue} {unit}</span>
  </div>)
}